<template>
  <div class="summary">
    <div class="search-form">
      <Form ref="formValidate" :model="searchData" inline :label-width="40">
        <FormItem label="日期" prop="startTime" >
          <DatePicker  v-model="searchData.startTime" type="date" placeholder="开始时间" style="width: 200px"></DatePicker>
        </FormItem>
        <FormItem label="—" class="date-line" prop="endTime" :label-width="20">
          <DatePicker v-model="searchData.endTime" type="date" placeholder="结束时间" style="width: 200px"></DatePicker>
        </FormItem>
        <FormItem label="姓名" prop="studentId">
          <Select clearable style="width: 160px" v-model="searchData.studentId" placeholder="学生姓名">
            <Option v-for="(label,value) in studentsMap" :key="value" :value="value">{{label}}</Option>
          </Select>
        </FormItem>
        <FormItem :label-width="20">
          <Button @click="reset()">重置</Button>
          <Button type="primary" icon="ios-cloud-upload-outline" style="margin-left:8px" @click="exportAction()">导出</Button>
        </FormItem>
      </Form>
    </div>
    <div class="table-part">
      <Table ref="table" height="740" stripe border :columns="columns" :data="dealData"></Table>
    </div>
  </div>
</template>
<script>

import { dates, students, subjects, classes } from '../../mixins/index.js'
export default {
  mixins: [dates, students, subjects, classes],
  data () {
    return {
      searchData: {
        startTime: '',
        endTime: '',
        studentId: ''
      },
      columns: [{
        title: '序号',
        type: 'index',
        width: 100,
        align: 'center'
      }, {
        title: '上课日期',
        key: 'date',
        align: 'center'
      }, {
        title: '上课时间',
        key: 'periods',
        align: 'center'
      }, {
        title: '姓名',
        key: 'studentName',
        align: 'center'
      }, {
        title: '科目',
        key: 'subject',
        align: 'center'
      }, {
        title: '小时数',
        key: 'count',
        align: 'center'
      }],
      data: [],
      tableName: '课时统计表'
    }
  },
  computed: {
    dealData () {
      let { startTime, endTime, studentId } = this.searchData
      return this.allClassData.filter(item => {
        item.count = item.periods.length * 0.5
        item.subject = this.subjectMaps[item.subject]
        item.periods = this.dealPeriods(item.periods)
        return (startTime ? this.RD(startTime, item.date) : true) && (endTime
          ? !this.RD(endTime, item.date) : true) && (studentId ? String(studentId) === String(item.studentId) : true)
      })
    }
  },
  created () {
    this.$indexedDB.initStore('curriculum', this.query)
  },
  methods: {
    exportAction () {
      let self = this
      this.$Modal.confirm({
        render: (h) => {
          return h('Input', {
            props: {
              value: this.tableName,
              autofocus: true,
              placeholder: '输入表格名称'
            },
            on: {
              input: (val) => {
                this.tableName = val
              }
            }
          })
        },
        onOk () {
          self.exportTable()
        }
      })
    },
    exportTable () {
      this.$refs.table.exportCsv({
        filename: this.tableName
      })
    },
    reset () {
      this.$refs.formValidate.resetFields()
    }
  }
}
</script>
